/**
 * 
 * :
 * request('',{key:value},'(GET)')
 * .then(res=>{console.log(res)})
 */
let baseUrl = 'http://172.20.10.5:9314';
async function request(mehtod, params, type, showLoading) {
	//request
	if (!type) {
		type = 'GET';
	}
	let url = baseUrl + mehtod
	return new Promise((resolve, reject) => {
		if (showLoading) {
			uni.showLoading({
				title: ''
			});
		}

		uni.request({
			"method": type,
			"url": url,
			"header": {
				'Authorization': 'Bearer ' + uni.getStorageSync('token'),
				'client-type': 'MA', //
			},
			"data": params,
			success(res) {
				if (res.statusCode == 200) {
					if (res.data.code === 401) {
						uni.hideTabBarRedDot({
							index: 1
						})
						let pages = getCurrentPages(); // 
						var currentPage = pages[pages.length - 1].route; // 
						console.log('currentPage===>',currentPage)
						uni.setStorageSync('currentPage',currentPage)
						uni.showModal({
							title: '',
							content: '',
							cancelText: '',
							confirmText: '',
							success: res => {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/public/login'
									})
								}
								try {
									uni.removeStorageSync('token')
									uni.removeStorageSync('userInfo')
								} catch (e) {
									// error
									console.log(e)
								}
							},
						})
						reject("!");
					} else if (res.data.code === 200) {
						let data = res.data
						if (data.hasOwnProperty("token")) {
							let token = data.token
							uni.setStorageSync("token", data.token)
							uni.setStorageSync("userInfo", data.userInfo)
							uni.setStorageSync("cartLength", data.cartLength)
						}
						resolve(res.data);
					} else if (res.statusCode == 404) {
						uni.showModal({
							title: '',
							content: '',
							success(res) {}
						});
						reject();
					}else {
						console.log(res);
						uni.showModal({
							title: '',
							content: '' + res.data.msg,
							success(res) {}
						});
						reject();
					}
				}
			},
			fail(error) {
				console.log(error);
				uni.showModal({
					title: '',
					content: '' + error.errMsg,
					success(res) {}
				});
				reject(error);
			},
			complete(res) {
				uni.hideLoading();
			}
		});
	}).catch(e => {});
}
